<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/program')" class="current">프로그램</li>
			<li @click="goto('/admin/goods')">굿즈</li>
		</ul>
		<div class="contents-container">
			<div class="list-contianer">
				<div
					class="list-item"
					:class="{ mainBackground: item.mainYn === 'Y' }"
					v-for="(item, index) in programList"
					:key="item.id"
				>
					<input
						type="checkbox"
						class="checkbox"
						name="member"
						:id="item.id"
						:value="{ id: item.id, title: item.title }"
						v-model="selected"
					/>
					<label :for="item.id" class="marB-20"></label>
					<div class="item-info">
						<p class="body3 clickable" @click.capture="handleClickItem(index)">
							{{ item.title }}
						</p>
						<p>{{ item.price }}</p>
						<!-- <p>10,000원</p></p> -->
						<p>{{ item.classDate }}</p>
						<!-- <p>옵션: 3개</p> -->
						<p>{{ getStatusValue(item.status) }}</p>
					</div>
					<ModalView
						v-if="isDetailModalViewed"
						@close-modal="isDetailModalViewed = false"
					>
						<ProgramDetail
							:program="programList[clickedId]"
							@close-modal="isDetailModalViewed = false"
						></ProgramDetail>
					</ModalView>
					<div class="item-btn-container">
						<router-link
							:to="{ name: 'EditProgram', params: { id: item.id } }"
							class="marR-5"
						>
							<button class="solid-btn green">
								수정하기
							</button>
						</router-link>
						<button
							v-if="item.mainYn === 'N'"
							class="solid-btn green"
							@click="updateProgramMainYn(item.id, 'Y')"
						>
							메인등록
						</button>
						<button
							v-else
							class="solid-btn green"
							@click="updateProgramMainYn(item.id, 'N')"
						>
							메인해제
						</button>
					</div>
				</div>
			</div>
			<div class="bottom-container border">
				<Pagination
					v-if="totalPage !== -1"
					:listRowCt="15"
					:pageLinkCt="5"
					:totalCount="totalPage"
					:url="`/admin/program`"
				/>
				<div class="btn-container">
					<button
						class="solid-btn red"
						:disabled="selectedIds.length < 1"
						@click="isDeleteModalViewed = true"
					>
						선택 프로그램 삭제
					</button>

					<button
						class="solid-btn green"
						:disabled="selectedIds.length < 1"
						@click="isUpdateModalViewed = true"
					>
						선택 프로그램 상태 변경
					</button>
					<router-link :to="{ name: 'AddProgram' }">
						<button class="solid-btn green" :disabled="selected.length >= 1">
							새 프로그램 등록
						</button>
					</router-link>
				</div>
			</div>

			<ModalView
				v-if="isDeleteModalViewed"
				@close-modal="isDeleteModalViewed = false"
			>
				<DeleteItem
					@close-modal="isDeleteModalViewed = false"
					@confirm="deleteProgram"
					:count="selectedIds.length"
				></DeleteItem>
			</ModalView>
			<ModalView
				v-if="isUpdateModalViewed"
				@close-modal="isUpdateModalViewed = false"
			>
				<div class="update-status-container" :count="selectedIds.length">
					<div class="modal-contents">
						<p v-if="selectedName.length <= 1" class="body3 explanation">
							{{ selectedName[0] }}의 프로그램 상태를 어떻게 변경하시겠습니까?
						</p>
						<p v-else class="body3 explanation">
							{{ selectedName[0] }} 외 {{ selectedName.length - 1 }}의 프로그램
							상태를 어떻게 변경하시겠습니까?
						</p>
						<ul class="radio-container">
							<li class="radio-btn">
								<input
									type="radio"
									id="inProgress"
									value="INPROGRESS"
									name="status"
									@click="handleClickRadioBtn"
								/><label for="inProgress"
									><span class="inside-circle"></span></label
								><span class="btn-label body2">판매중</span>
							</li>
							<li class="radio-btn">
								<input
									type="radio"
									id="complete"
									value="COMPLETE"
									name="status"
									@click="handleClickRadioBtn"
								/><label for="complete"
									><span class="inside-circle"></span></label
								><span class="btn-label body2">품절</span>
							</li>
							<li class="radio-btn">
								<input
									type="radio"
									id="tumblBug"
									value="TUMBLBUG"
									name="status"
									@click="handleClickRadioBtn"
								/><label for="tumblBug"
									><span class="inside-circle"></span></label
								><span class="btn-label body2">텀블벅</span>
							</li>
						</ul>
					</div>
					<div class="modal-btn-container">
						<button @click="close">취소</button>
						<button type="button" @click="updateProgramStatus">확인</button>
					</div>
				</div>
			</ModalView>
		</div>
	</div>
</template>

<script>
import {
	fetchProgram,
	fetchProgramTotalCount,
	deleteProgram,
	updateProgramStatus,
	updateProgramMainYn,
} from '@/api/program';
import Pagination from '@/components/common/Pagination';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import ModalView from '@/components/common/ModalView';
import ProgramDetail from '@/views/admin/program-goods/components/ProgramDetail';

export default {
	components: {
		ProgramDetail,
		DeleteItem,
		ModalView,
		Pagination,
	},
	created() {
		this.currentPage = this.$route.params.page;
		this.fetchProgram(this.currentPage);
		this.fetchProgramTotalCount();
	},
	data() {
		return {
			programList: [],
			selected: [],
			selectedIds: [],
			selectedName: [],
			totalPage: -1,
			currentPage: 1,
			isDetailModalViewed: false,
			isUpdateModalViewed: false,
			isLoading: false,
			isDeleteModalViewed: false,
			clickedId: '',
			status: '',
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.programList
					? this.selected.length == this.programList.length
					: false;
			},
			set: function(value) {
				let selected = [];

				if (value) {
					this.programList.forEach(function(item) {
						console.log(item.id);
						selected.push(item);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		handleClickItem(index) {
			console.log('??');
			this.clickedId = index;
			this.isDetailModalViewed = true;
		},
		handleClickRadioBtn(e) {
			this.status = e.target.value;
		},

		close() {
			this.isUpdateModalViewed = false;
			this.isDeleteModalViewed = false;
		},
		getStatusValue(status) {
			let text = '';
			if (status === 'INPROGRESS') text = '판매중';
			if (status === 'COMPLETE') text = '품절';
			if (status === 'TUMBLBUG') text = '텀블벅';
			return text;
		},
		async fetchProgram(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchProgram(page - 1);
				this.programList = data.programList;
			} catch (e) {
				console.log(e);
				alert(e);
			}
		},
		async fetchProgramTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchProgramTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
		async updateProgramMainYn(id, mainYn) {
			this.isLoading = true;
			try {
				await updateProgramMainYn(id, mainYn);
				await this.fetchProgram(this.currentPage);
			} catch (e) {
				alert(e);
			}
			this.isLoading = false;
		},
		async updateProgramStatus() {
			this.isLoading = true;
			try {
				await updateProgramStatus(this.status, this.selectedIds);
				await this.fetchProgram(this.currentPage);
				this.isUpdateModalViewed = false;
			} catch (e) {
				alert(e);
			}
			this.isLoading = false;
		},
		async deleteProgram() {
			this.isLoading = true;
			try {
				await deleteProgram(this.selectedIds);
				await this.fetchProgram(this.currentPage);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.fetchProgram(this.currentPage);
			}
		},
		selected: function() {
			this.selectedIds = this.selected.map(item => item.id);
			this.selectedName = this.selected.map(item => item.title);
		},
	},
};
</script>

<style lang="scss" scoped>
.list-contianer {
	display: flex;
}
.list-item {
	width: 250px;
	height: 282px;
	background: #fafafa;
	border-radius: 4px;
	padding: 20px;
	box-sizing: border-box;
	margin-right: 25px;
}

.clickable {
	cursor: pointer;
}

.list-item {
}

.list-item.mainBackground {
	background: #fffbe5;
}

.list-item:nth-child(4n) {
	margin-right: 0;
}

.list-item .item-info p:first-child {
	margin-top: 20px;
}

.list-item .item-info p:nth-child(4) {
	margin-bottom: 16px;
}

.list-item .item-info p {
	line-height: 30px;
	margin-bottom: 8px;
}

.item-btn-container {
	display: flex;
}

.radio-container {
	margin-top: 24px;
	display: flex;
	align-items: center;
}
.radio-container .radio-btn {
	display: flex;
	align-items: center;
	margin-right: 13px;
}

.radio-btn input[type='radio'] + label {
	margin-right: 8px;
}

.update-status-container {
	width: 430px;
}
.modal-contents {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 162px;
}
.modal-contents .explanation {
	width: 386px;
	text-align: center;
}

.modal-btn-container {
	height: 57px;
}

.modal-btn-container button {
	width: 50%;
	height: 100%;
}
.modal-btn-container button:nth-child(1) {
	border-right: 1px solid #fff;
}
</style>
