<template>
	<ul class="pagination justify-content-center">
		<li class="page-item">
			<router-link
				class="page-link"
				aria-label="Previous"
				@click.native="movePage(startPageIndex - 1)"
				v-if="prev"
			>
				<LeftArrow />
			</router-link>
			<LeftArrow class="disabled" v-if="!prev" />
		</li>

		<li
			v-for="index in endPageIndex - startPageIndex + 1"
			:key="index"
			class="page-item body2"
		>
			<a
				class="page-link body2"
				:class="{ active: startPageIndex + index - 1 == currentPageIndex }"
				@click="movePage(startPageIndex + index - 1)"
				>{{ startPageIndex + index - 1 }}</a
			>
		</li>

		<li class="page-item">
			<router-link
				class="page-link"
				aria-label="Next"
				v-if="next"
				@click.native="movePage(endPageIndex + 1)"
			>
				<RightArrow />
			</router-link>

			<RightArrow class="disabled" v-if="!next" />
		</li>
	</ul>
</template>

<script>
import LeftArrow from '@/assets/icon/LeftArrow';
import RightArrow from '@/assets/icon/RightArrow';
export default {
	created() {
		this.initComponent();
	},
	name: 'row',
	components: { RightArrow, LeftArrow },
	data() {
		return {
			totalListItemCount: 0,
			listRowCount: 10,
			pageLinkCount: 5,
			currentPageIndex: 1,
			pageCount: 0,
			startPageIndex: 0,
			endPageIndex: 0,
			prev: false,
			next: false,
		};
	},
	props: {
		listRowCt: {
			type: Number,
		},
		pageLinkCt: {
			type: Number,
		},
		totalCount: {
			type: Number,
			required: true,
		},
		url: {
			type: String,
			required: true,
		},
	},
	methods: {
		movePage(param) {
			this.currentPageIndex = param;
			this.$router.push(this.url + '/' + this.currentPageIndex);
			this.initComponent();
		},

		initComponent() {
			this.totalListItemCount = this.totalCount;
			this.initUI();
		},
		initUI() {
			this.pageCount = Math.ceil(this.totalListItemCount / this.listRowCount);
			if (this.currentPageIndex % this.pageLinkCount == 0) {
				this.startPageIndex =
					(this.currentPageIndex / this.pageLinkCount - 1) *
						this.pageLinkCount +
					1;
			} else {
				this.startPageIndex =
					Math.floor(this.currentPageIndex / this.pageLinkCount) *
						this.pageLinkCount +
					1;
			}

			if (this.currentPageIndex % this.pageLinkCount == 0) {
				//10, 20...맨마지막
				this.endPageIndex =
					(this.currentPageIndex / this.pageLinkCount - 1) *
						this.pageLinkCount +
					this.pageLinkCount;
			} else {
				this.endPageIndex =
					Math.floor(this.currentPageIndex / this.pageLinkCount) *
						this.pageLinkCount +
					this.pageLinkCount;
			}

			if (this.endPageIndex > this.pageCount) {
				this.endPageIndex = this.pageCount;
			}

			if (this.currentPageIndex <= this.pageLinkCount) {
				this.prev = false;
			} else {
				this.prev = true;
			}

			if (this.endPageIndex >= this.pageCount) {
				this.endPageIndex = this.pageCount;
				this.next = false;
			} else {
				this.next = true;
			}
		},
	},
	watch: {
		currentPageIndex: function() {
			this.initUI();
		},
	},

	mounted() {
		// this.$router.push('' + this.currentPage);
	},
};
</script>
<style scoped>
.pagination {
	display: flex;
}
.pagination li {
	margin-right: 20px;
}
.page-link {
	color: #8c8c8c;
	cursor: pointer;
}
.active {
	color: #000;
}
</style>
