// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchProgramTotalCount() {
	return instance.get(`api/program/totalCount`);
}

function fetchProgram(page) {
	return instance.get(`api/program/all/${page}`);
}

function fetchProgramDetail(id) {
	return instance.get(`api/program/detail/${id}`);
}

function addProgram(data) {
	return instanceWithAuth.post('api/admin/program/new', data);
}

function updateProgram(id, data) {
	return instanceWithAuth.put(`api/admin/program/update/${id}`, data);
}

function deleteProgram(data) {
	return instanceWithAuth.delete('api/admin/program/delete', { data });
}
function updateProgramMainYn(id, mainYn) {
	return instanceWithAuth.put(`/api/admin/program/setMainYn/${id}/${mainYn}`);
}
function updateProgramStatus(status, data) {
	return instanceWithAuth.put(
		`/api/admin/program/updateStatus/${status}`,
		data,
	);
}

export {
	fetchProgramTotalCount,
	fetchProgram,
	fetchProgramDetail,
	addProgram,
	updateProgram,
	updateProgramMainYn,
	updateProgramStatus,
	deleteProgram,
};
